// FONT DTL PROKYON
// ================================================================

@font-face {
    font-family: DTL Prokyon;
    src: url('/assets/fonts/dtl-prokyon/DTL-Prokyon-Light.ttf') format("truetype");
    font-weight: 100;
    font-style: normal
}

@font-face {
    font-family: DTL Prokyon;
    src: url('/assets/fonts/dtl-prokyon/DTL-Prokyon-Medium.ttf') format("truetype");
    font-weight: 400;
    font-style: normal
}

@font-face {
    font-family: DTL Prokyon;
    src: url('/assets/fonts/dtl-prokyon/DTL-Prokyon-Bold.ttf') format("truetype");
    font-weight: bold;
    font-style: normal
}

.ebook-landing {
    display: table;
    height: 100vh;

    .nav {
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;        
        text-align: center;
        background-color: rgba(0, 0, 0, 0.35);

        img {
            padding: 15px;            

            @media (max-width: 767px) {
                height: 70px;
            }
        }
    }

    .body {        
        padding: 60px 16px 90px 16px;
        overflow: hidden;
        display: table-cell;
        vertical-align: middle;

        &.django {
            background-image: url("/assets/svg/stars-bground.svg"), linear-gradient(to bottom, #075638, #01150d);
        }

        &.listas {
            background: url("/assets/svg/stars-bground.svg"), #599ebf;
        }

        &.linkedin {
            background: url("/assets/svg/stars-bground.svg"), #069;
        }

        &.drf {
            background: url("/assets/svg/stars-bground.svg"),linear-gradient(to bottom, #0f0e15, #0a090e)
        }        

        form.ebook-form {
            label {

            }

            input {

            }

            button {

            }
        }

        .form-wrapper {
            h1 {
                font-size: 5rem;
                font-family: 'Muli', sans-serif;
                font-weight: bold;
                letter-spacing: -0.4px;
                color: #FFF;
            }

            h2 {
                font-family: 'Roboto Condensed', sans-serif;
                text-transform: uppercase;
                color: #fffd;
                font-size: 2rem;
                font-weight: bold;
                margin-top: 0;
            }

            h3 {
                font-family: 'Roboto Condensed', sans-serif;
                color: #FFF;
                font-size: 2rem;
            }
            
            .padding {
                padding: 30px 30px 0 30px;
            }
        }

        .form-wrapper {
            .form-description {
                padding-right: 40px;

                h1 {
                    font-family: 'Roboto Condensed', sans-serif;
                    font-size: 3rem;
                    color: #FFF;
                    font-weight: 400;
                    margin-bottom: 32px;
                }

                h2 {
                    font-family: 'Roboto Condensed', sans-serif;
                    font-size: 1.7rem;
                    letter-spacing: 1px;
                    text-transform: uppercase;
                    color: #44b77e;
                    font-weight: bold;
                    margin-top: 0;
                }

                p {
                    font-family: 'Lato', sans-serif;
                    font-size: 2rem;
                    color: #eee;
                    margin-bottom: 16px;

                    .django {
                        font-family: 'DTL Prokyon', sans-serif;
                        color: #44b78b;
                        font-weight: bold;
                    }
                }

                ul {
                    list-style: none;
                    li {
                        font-family: 'Lato', sans-serif;
                        font-size: 1.8rem;
                        color: #eee;
                        margin-bottom: 14px;
                    }
                }
            }

            .form-container {
                width: 100%;
                margin: 0 0 0 auto;
                border-radius: 16px;                        
                background: #FFF;
                text-align: center;
                position: relative;
                margin-top: 39px;
                box-shadow: 0px 6px 24px rgba(0, 0, 0, 0.35);            
    
                .form-header {
                    background: #032518;
                    border-radius: 10px 10px 0 0;
                    position: absolute;
                    top: 0;
                    z-index: 999;
                    width: 100%;
    
                    h2 {
                        font-family: 'DTL Prokyon', sans-serif;
                        font-weight: bold;
                        letter-spacing: -0.25px;
                        color: #FFF;
                        font-size: 2rem;
                        padding: 24px 12px;
                        margin: 0;

                        @media (max-width: 767px) {
                            font-size: 1.65rem;
                            padding: 12px;
                            letter-spacing: 0.5px;
                        }
                    }
    
                    &:after {
                        content: ' ';
                        position: absolute;
                        background: url('/assets/ebooks/desenvolvimento-web-com-python-e-django/arrow-down.png') center center no-repeat;
                        bottom: -16px;
                        left: calc(50% - 18px);
                        width: 36px;
                        height: 16px;
                    }
                }
    
                .form-body {
                    padding: 110px 32px 52px 32px;

                    input {
                        width: 100%;
                        margin-bottom: 24px;
                        padding: 11px;
                        font-size: 13px;
                        border-radius: 5px;
                        border: 2px solid #ccc;
                        font-family: 'Lato', sans-serif;

                        &:focus, &:active {
                            outline: none;
                            border-color: #44b77e;
                        }

                        @media (max-width: 767px) {
                            margin-bottom: 12px;
                        }
                    }
    
                    h3 {
                        font-family: 'Roboto Condensed', sans-serif;
                        font-weight: bold;
                        font-size: 1.45rem;
                        text-transform: uppercase;
                        letter-spacing: 1px;
                        color: #999;
                        margin: 0;
                    }
        
                    h4 {
                        font-family: 'Roboto Condensed', sans-serif;
                        font-weight: bold;
                        font-size: 5rem;
                        color: #44b77e;
                        letter-spacing: -0.3px;
                    }
        
                    button {
                        border: 0;
                        background: #44B78B;
                        border-radius: 8px;                    
                        font-family: 'Lato', sans-serif;
                        text-transform: uppercase;
                        font-weight: bold;                                                
                        color: #FFF;
                        padding: 18px 30px;
                        width: 100%;      
                        
                        @media (max-width: 767px) {
                            padding: 12px 30px;
                        }
                    }

                    .ebook-cover {
                        @media (max-width: 767px) {
                            width: 35%;
                        }

                        @media (max-width: 550px) {
                            width: 65%;
                        }
                    }

                    p.privacy {
                        font-family: 'Lato', sans-serif;
                        font-size: 14px;
                        font-weight: 600;
                        margin-top: 12px;

                        img {
                            margin-right: 6px;
                        }
                    }   
                    
                    #mc_embed_signup {
                        padding: 56px 0;

                        @media (max-width: 767px) {
                            padding: 12px 0;
                        }
                    }

                    @media (max-width: 767px) {
                        padding: 100px 32px 12px 32px;
                    }

                }               
                
                .form-bground {
                    width: 100%;
                    display: block;
                    z-index: -1;
                    position: absolute;
                    bottom: 0;
                    border-radius: 0 0 10px 10px;
                    height: 23vh;
                    opacity: 0.3;
                }

                @media (max-width: 767px) {
                    margin-top: 28px;                    
                }
            }
        }

        @media (max-width: 767px) {
            padding: 70px 16px 16px 16px;
        }
    }

    footer {
        position: absolute;
        width: 100%;
        bottom: 0;
        left: 0;
        padding: 12px;

        p {
            font-family: 'Muli', sans-serif;
            font-size: 1.5rem;
            margin: 0;
            color: #FFFD;
        }
    }
}

